*{
    font-family: Montserrat;
}
.navbar{
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 0;
    position: fixed;
}
.logo{
    margin-right: 1.5vw;
}
.brand{
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
}
.menu{
    background-color:#E94560;
    outline: none ;
    border:0;
    color: #fff;
    border-radius: 0;
    height: 55px;
    margin-top: 0;
    z-index: 4;
}.menu:focus{
    outline: none;
}
.menu:active{
    background-color: #fff;
}
.menu .icon{
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}

.menuWrapper{
    z-index: 2;
    position: absolute;
    width: 100%;
    min-height: 100vh;
    background-color:  #E94560;
    transition: margin-top 0.1s ease-in-out;
    transition-delay: 0.2s;
    margin-top: -100vh;
}
.menuWrapper.is-nav-open{
    margin-top: 0;
}
@media screen and (max-width:768px){
    .navbar{
        padding-left: 20px;
        padding-right: 20px;
    }
  }