.App {
  text-align: left;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-left: 50px;
}

.App-logo {
  height: 90vmin;
  opacity: 20%;
  pointer-events: none;
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
}
.section{
  z-index: -1;
}


/* My Custom Right Navigation */
#fp-nav{
  z-index: 1;
  top: 90%;
}
#fp-nav.fp-right {
  right: 50px;
}
#fp-nav ul li a span, .fp-slidesNav ul li a span{
  background-color: #E94560;
  height: 8px;
  width: 8px;
  margin: -2px 0 0 -2px;
  transition: all .3s ease-in-out;
}

#fp-nav ul li:hover a.active span, #fp-nav ul li a.active span, .fp-slidesNav ul li:hover a.active span, .fp-slidesNav ul li a.active span{
  
  color: #000;
  background-color: #fff;
  height: 12px;
  width: 200px;
  margin: -6px 0 0 -6px;
  border-radius: 0px;
}

