.Menu{
    z-index: 2;
    position: absolute;
    width: 100%;
    min-height: 100vh;
    background-color:  #E94560;
    transition: margin-top 0.5s;
}
.menuli{
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.menuli ul{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.menuli li{
    text-align: center;
    list-style: none;
    font-size: calc(10px + 3vmin);
    margin: 2rem;
}
.menuli a{
    text-decoration: none;
    color: #fff;
    position: relative;
}.menuli a:hover{
    text-decoration: none;
    color: #fff; 
}
.menuli li a::before{
    content: "";
    position: absolute;
    width: 0;
    height: 5px;
    background-color: #fff;
    margin-top: 2rem; 
    transition: width 0.1s ease-in-out;
    transition-delay: 0.2s;
}.menuli li a:hover::before{ 
    width: 100%;
}