*::selection{
    background-color: #E94560;
    color:#fff;
}
.hello{
    font-weight: bold;
    font-size: calc(50px + 2vmin);
    line-height: 1;
    color:#0F3460;
}
.im{
    font-weight: bolder;
    font-size: calc(50px + 3vmin);
    line-height: 1;
}
.im .vert{
    font-size: calc(50px + 3vmin);
    line-height: 1;
    color: #E94560;
}
.line{
    margin-top: 1rem;
    margin-bottom: 1rem;
    background-color: #E94560;
    height: 5px;
    width: 250px;
}
.txt{
    font-size: calc(20px + 1vmin);
    line-height: 1.5;
}
.cta{
    outline: none;
    width: 250px;
    font-size: calc(10px + 1vmin);
    position: relative;
    color:#fff;
    background-color: #E94560;
    border: 0;
    border-radius: 0;
    line-height: 50px;
    z-index: 1;
    overflow: hidden;
    transition: color 0.5s cubic-bezier(0.19, 1, 0.22, 1),
        transform 0.75s cubic-bezier(0.19, 1, 0.22, 1);
}
.cta:focus{
    outline: none;
    color:#0F3460;
    background-color: #fff;
}.cta span{
    position: relative;
    z-index: 1;
}.cta:hover{
    transition-duration: 1s;
    color:#0F3460;
}
.cta::before{
    content: "";
    position: absolute;
    width: 250px;
    height: 100%;
    top: 50%;
    transform: translate3d(-50%,-50%,0) scale3d(0,0,0);
    transition: margin-left 0.1s cubic-bezier(0.19, 1, 0.22, 1),
        transform 0.1s cubic-bezier(0.19, 1, 0.22, 1);
    background-color:  #fff;
    margin-left: -100%;
}.cta:hover::before{
    margin-left:0 ;
    transition-duration: 1s;
    transform: translate3d(-50%,-50%,0) scale3d(2,2,2);
}